import Button from '@material-ui/core/Button';
import Collapse from '@material-ui/core/Collapse';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';
import PropTypes from 'prop-types';
import React, { useState } from 'react';

import styles from './ResearchPaper.css';

const ResearchPaper = ({ abstract, href, title }) => {
  const classes = makeStyles(styles)();
  const [abstractVisible, setAbstractVisible] = useState(false);

  return (
    <Paper className={classes.paper}>
      <Typography className={classes.title} variant="subtitle1">
        {title}
      </Typography>
      <Collapse in={abstractVisible}>
        <Typography
          className={classes.abstract}
          color="textSecondary"
          variant="body2">
          {abstract}
        </Typography>
      </Collapse>
      <div className={classes.actions}>
        <Button
          className={classes.action}
          color="secondary"
          onClick={() => {
            setAbstractVisible((prev) => {
              return !prev;
            });
          }}>
          {abstractVisible ? `Hide` : `Read`} Abstract
        </Button>
        <Button
          className={classes.action}
          color="secondary"
          component="a"
          href={href}
          rel="noopener noreferrer"
          target="_blank">
          Read Full Paper
        </Button>
      </div>
    </Paper>
  );
};

ResearchPaper.propTypes = {
  abstract: PropTypes.string.isRequired,
  href: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default ResearchPaper;
